import React from 'react';
import { Link } from 'react-router-dom';
import Navbar from './Navbar';

function Header() {
    return (
        <>
            <header>
                {/* Header Start */}
                <div className="header-area">
                    <div className="main-header">
                        <div className="header-top black-bg d-none d-sm-block">
                            <div className="container">
                                <div className="col-lg-12">
                                    <div className="row d-flex justify-content-between align-items-center pt-2">
                                        <div className="header-info-left col-lg-8">
                                            <ul>
                                                <li className="title"> trending-title</li>
                                                <li>Class property employ ancho red multi level mansion</li>
                                            </ul>
                                        </div>
                                        <div className="header-info-right col-lg-4 text-end">
                                            <ul className="header-date">
                                                <Link to={'/'}><li className='pe-2'><i class="fa fa-phone pe-1"></i> +880166 253 232</li></Link>
                                                <Link to={'/'}><li><i className="fa fa-envelope pe-1"></i>news@commercial.org</li></Link>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {/* Header End */}
            </header>


            <Navbar />
        </>
    )
}

export default Header