import React from 'react';
import {Link} from 'react-router-dom';
import { Follow, Info, Recent } from '../Data/HomeData';

function HomeNew() {
    return (
        <>
            <section className="whats-news-area gray-bg py-5">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-8">
                            <div className="whats-news-wrapper">
                                {/* Heading & Nav Button */}
                                <div className="row justify-content-between align-items-end mb-15">
                                    <div className="col-xl-4">
                                        <div className="section-tittle mb-4">
                                            <h3>What's New</h3>
                                        </div>
                                    </div>
                                    <div className="col-xl-8 col-md-9">
                                        <div className="properties__button">
                                            {/*Nav Button  */}
                                            <nav>
                                                <div className="nav nav-tabs" id="nav-tab" role="tablist">
                                                    <Link className="nav-item nav-link active" id="nav-home-tab" data-bs-toggle="tab" to="#nav-home" role="tab" aria-controls="nav-home" aria-selected="true">Lifestyle</Link>
                                                    <Link className="nav-item nav-link" id="nav-travel-tab" data-bs-toggle="tab" to="#nav-travel" role="tab" aria-controls="nav-travel" aria-selected="false">Travel</Link>
                                                    <Link className="nav-item nav-link" id="nav-fashion-tab" data-bs-toggle="tab" to="#nav-fashion" role="tab" aria-controls="nav-fashion" aria-selected="false">Fashion</Link>
                                                    <Link className="nav-item nav-link" id="nav-technology-tab" data-bs-toggle="tab" to="#nav-technology" role="tab" aria-controls="nav-fashion" aria-selected="false">Technology</Link>
                                                    <Link className="nav-item nav-link" id="nav-Sports" data-bs-toggle="tab" to="#nav-Sport" role="tab" aria-controls="nav-fashion" aria-selected="false">Sports</Link>
                                                </div>
                                            </nav>
                                            {/*End Nav Button  */}
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-12">
                                        <div className="tab-content" id="nav-tabContent">
                                            <div className="tab-pane fade show active" id="nav-home" role="tabpanel" aria-labelledby="nav-home-tab">
                                                <div className="row">
                                                    <div className="col-xl-6 col-lg-12">
                                                        <div className="whats-news-single mb-4">
                                                            <div className="whates-img">
                                                                <img src={require('../../assets/img/gallery/whats_news_details1.png')} alt="" />
                                                            </div>
                                                            <div className="whates-caption">
                                                                <h4><Link to={'/'}>Secretart for Economic Air plane that looks like</Link></h4>
                                                                <span>by Alice cloe   -   Jun 19, 2023</span>
                                                                <p>Struggling to sell one multi-million dollar home currently on the market won’t stop actress and singer Jennifer Lopez.</p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="col-xl-6 col-lg-12">
                                                        <div className="row">
                                                            {Info.map((info)=>(
                                                            <div className="col-xl-12 col-lg-6 col-md-6 col-sm-10">
                                                                <div className="whats-right-single mb-2">
                                                                    <div className="whats-right-img">
                                                                        <img src={info.image} alt="" />
                                                                    </div>
                                                                    <div className="whats-right-cap">
                                                                        <span className="colorb">{info.title}</span>
                                                                        <h4><Link to={'/'}>{info.description}</Link></h4>
                                                                        <p>{info.date}</p>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            ))}
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="tab-pane fade" id="nav-travel" role="tabpanel" aria-labelledby="nav-travel-tab">
                                                <div className="row">
                                                    <div className="col-xl-6 col-lg-12">
                                                        <div className="whats-news-single mb-4">
                                                            <div className="whates-img">
                                                                <img src={require('../../assets/img/gallery/whats_news_details2.png')} alt="" />
                                                            </div>
                                                            <div className="whates-caption">
                                                                <h4><Link to={'/'}>Secretart for Economic Air plane that looks like</Link></h4>
                                                                <span>by Alex cloe   -   Jun 19, 2023</span>
                                                                <p>Struggling to sell one multi-million dollar home currently on the market won’t stop actress and singer Jennifer Lopez.</p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="col-xl-6 col-lg-12">
                                                        <div className="row">
                                                            {Info.map((info)=>(
                                                            <div className="col-xl-12 col-lg-6 col-md-6 col-sm-10">
                                                                <div className="whats-right-single mb-2">
                                                                    <div className="whats-right-img">
                                                                        <img src={info.image} alt="" />
                                                                    </div>
                                                                    <div className="whats-right-cap">
                                                                        <span className="colorb">{info.title}</span>
                                                                        <h4><Link to={'/'}>{info.description}</Link></h4>
                                                                        <p>{info.date}</p>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            ))}
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="tab-pane fade" id="nav-fashion" role="tabpanel" aria-labelledby="nav-fashion-tab">
                                                <div className="row">
                                                    <div className="col-xl-6 col-lg-12">
                                                        <div className="whats-news-single mb-4">
                                                            <div className="whates-img">
                                                                <img src={require('../../assets/img/gallery/whats_news_details3.png')} alt="" />
                                                            </div>
                                                            <div className="whates-caption">
                                                                <h4><Link to={'/'}>Secretart for Economic Air plane that looks like</Link></h4>
                                                                <span>by Alice cloe   -   Jun 19, 2023</span>
                                                                <p>Struggling to sell one multi-million dollar home currently on the market won’t stop actress and singer Jennifer Lopez.</p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="col-xl-6 col-lg-12">
                                                        <div className="row">
                                                            {Info.map((info)=>(
                                                            <div className="col-xl-12 col-lg-6 col-md-6 col-sm-10">
                                                                <div className="whats-right-single mb-2">
                                                                    <div className="whats-right-img">
                                                                        <img src={info.image} alt="" />
                                                                    </div>
                                                                    <div className="whats-right-cap">
                                                                        <span className="colorb">{info.title}</span>
                                                                        <h4><Link to={'/'}>{info.description}</Link></h4>
                                                                        <p>{info.date}</p>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            ))}
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="tab-pane fade" id="nav-technology" role="tabpanel" aria-labelledby="nav-technology-tab">
                                                <div className="row">
                                                    <div className="col-xl-6 col-lg-12">
                                                        <div className="whats-news-single mb-4">
                                                            <div className="whates-img">
                                                                <img src={require('../../assets/img/gallery/whats_news_details4.png')} alt="" />
                                                            </div>
                                                            <div className="whates-caption">
                                                                <h4><Link to={'/'}>Secretart for Economic Air plane that looks like</Link></h4>
                                                                <span>by Alice cloe   -   Jun 19, 2023</span>
                                                                <p>Struggling to sell one multi-million dollar home currently on the market won’t stop actress and singer Jennifer Lopez.</p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="col-xl-6 col-lg-12">
                                                        <div className="row">
                                                            {Info.map((info)=>(
                                                            <div className="col-xl-12 col-lg-6 col-md-6 col-sm-10">
                                                                <div className="whats-right-single mb-2">
                                                                    <div className="whats-right-img">
                                                                        <img src={info.image} alt="" />
                                                                    </div>
                                                                    <div className="whats-right-cap">
                                                                        <span className="colorb">{info.title}</span>
                                                                        <h4><Link to={'/'}>{info.description}</Link></h4>
                                                                        <p>{info.date}</p>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            ))}
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="tab-pane fade" id="nav-Sport" role="tabpanel" aria-labelledby="nav-Sport-tab">
                                                <div className="row">
                                                    <div className="col-xl-6 col-lg-12">
                                                        <div className="whats-news-single mb-4">
                                                            <div className="whates-img">
                                                                <img src={require('../../assets/img/gallery/whats_news_details1.png')} alt="" />
                                                            </div>
                                                            <div className="whates-caption">
                                                                <h4><Link to={'/'}>Secretart for Economic Air plane that looks like</Link></h4>
                                                                <span>by Alice cloe   -   Jun 19, 2023</span>
                                                                <p>Struggling to sell one multi-million dollar home currently on the market won’t stop actress and singer Jennifer Lopez.</p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="col-xl-6 col-lg-12">
                                                        <div className="row">
                                                            {Info.map((info)=>(
                                                            <div className="col-xl-12 col-lg-6 col-md-6 col-sm-10">
                                                                <div className="whats-right-single mb-2">
                                                                    <div className="whats-right-img">
                                                                        <img src={info.image} alt="" />
                                                                    </div>
                                                                    <div className="whats-right-cap">
                                                                        <span className="colorb">{info.title}</span>
                                                                        <h4><Link to={'/'}>{info.description}</Link></h4>
                                                                        <p>{info.date}</p>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            ))}
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {/* Banner */}
                            <div className="banner-one my-3">
                                <img src={require('../../assets/img/gallery/body_card1.png')} alt="" />
                            </div>
                        </div>
                        <div className="col-lg-4">
                            {/* Flow Socail */}
                            <div className="single-follow mb-45">
                                <div className="single-box">
                                    {Follow.map((follow)=>(
                                        <div className="follow-us d-flex align-items-center">
                                        <div className="follow-social">
                                            <Link to={'/'}><img src={follow.image} alt="" /></Link>
                                        </div>
                                        <div className="follow-count">
                                            <span>{follow.follower}</span>
                                            <p>{follow.title}</p>
                                        </div>
                                    </div>
                                    ))}
                                    
                                </div>
                            </div>
                            {/* Most Recent Area */}
                            <div className="most-recent-area">
                                {/* Section Tittle */}
                                <div className="small-tittle mb-2">
                                    <h4>Most Recent</h4>
                                </div>
                                <div className="most-recent mb-4">
                                    <div className="most-recent-img">
                                        <img src={require('../../assets/img/gallery/most_recent.png')} alt="" />
                                        <div className="most-recent-cap">
                                            <span className=" tag-orange">Vogue</span>
                                            <h4><Link to={'/'}>What to Wear: 9+ Cute Work <br />
                                                Outfits to Wear This.</Link></h4>
                                            <p>Jhon  |  2 hours ago</p>
                                        </div>
                                    </div>
                                </div>
                                {Recent.map((recent)=>(
                                    <div className="most-recent-single">
                                    <div className="most-recent-images">
                                        <img src={recent.image} alt="" />
                                    </div>
                                    <div className="most-recent-capt">
                                        <h4><Link to={'/'}>{recent.title}</Link></h4>
                                        <p>{recent.user}  | {recent.date}</p>
                                    </div>
                                </div>
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            </section>

        </>
    )
}

export default HomeNew