import React from 'react'
import { Link } from 'react-router-dom'
import { Cdata } from '../Data/CategoryData'
import { Follow } from '../Data/HomeData'

function CategoryBanner() {
    return (
        <>
            <div className="about-area2 gray-bg py-5">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-8">
                            <div className="whats-news-wrapper">
                                {/* Heading & Nav Button */}
                                <div className="row justify-content-between align-items-end mb-15">
                                    <div className="col-xl-4">
                                        <div className="section-tittle mb-4">
                                            <h3>Whats New</h3>
                                        </div>
                                    </div>
                                    <div className="col-xl-8 col-md-9">
                                        <div className="properties__button">
                                            {/*Nav Button  */}
                                            <nav>
                                                <div className="nav nav-tabs" id="nav-tab" role="tablist">
                                                    <Link className="nav-item nav-link active" id="nav-home-tab" data-bs-toggle="tab" href="#nav-home" role="tab" aria-controls="nav-home" aria-selected="true">Lifestyle</Link>
                                                    <Link className="nav-item nav-link" id="nav-travel-tab" data-bs-toggle="tab" href="#nav-travel" role="tab" aria-controls="nav-travel" aria-selected="false">Travel</Link>
                                                    <Link className="nav-item nav-link" id="nav-fashion-tab" data-bs-toggle="tab" href="#nav-fashion" role="tab" aria-controls="nav-fashion" aria-selected="false">Fashion</Link>
                                                    <Link className="nav-item nav-link" id="nav-technology-tab" data-bs-toggle="tab" href="#nav-technology" role="tab" aria-controls="nav-technology" aria-selected="false">Technology</Link>
                                                    <Link className="nav-item nav-link" id="nav-Sports" data-bs-toggle="tab" href="#nav-nav-Sport" role="tab" aria-controls="nav-Sport" aria-selected="false">Sports</Link>
                                                </div>
                                            </nav>
                                            {/*End Nav Button  */}
                                        </div>
                                    </div>
                                </div>
                                {/* Tab content */}
                                <div className="row">
                                    <div className="col-12">
                                        {/* Nav Card */}
                                        <div className="tab-content" id="nav-tabContent">
                                            {/* card one */}
                                            <div className="tab-pane fade show active" id="nav-home" role="tabpanel" aria-labelledby="nav-home-tab">
                                                <div className="row">
                                                    {Cdata.map((category) => (
                                                        <div className="col-xl-6 col-lg-6 col-md-6">
                                                            <div className="whats-news-single">
                                                                <div className="whates-img">
                                                                    <img src={category.image} alt=""/>
                                                                </div>
                                                                <div className="whates-caption whates-caption2">
                                                                    <h4><Link href="#">{category.title}</Link></h4>
                                                                    <span>{category.author}   -   {category.date}</span>
                                                                    <p>{category.description}</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    ))}
                                                </div>
                                            </div>
                                            {/* Card two */}
                                            <div className="tab-pane fade" id="nav-travel" role="tabpanel" aria-labelledby="nav-travel-tab">
                                                <div className="row">
                                                    {Cdata.map((category) => (
                                                        <div className="col-xl-6 col-lg-6 col-md-6">
                                                            <div className="whats-news-single">
                                                                <div className="whates-img">
                                                                    <img src={category.image} alt=""/>
                                                                </div>
                                                                <div className="whates-caption whates-caption2">
                                                                    <h4><Link href="#">{category.title}</Link></h4>
                                                                    <span>{category.author}   -   {category.date}</span>
                                                                    <p>{category.description}</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    ))}
                                                </div>
                                            </div>
                                            {/* Card three */}
                                            <div className="tab-pane fade" id="nav-fashion" role="tabpanel" aria-labelledby="nav-fashion-tab">
                                                <div className="row">
                                                    {Cdata.map((category) => (
                                                        <div className="col-xl-6 col-lg-6 col-md-6">
                                                            <div className="whats-news-single">
                                                                <div className="whates-img">
                                                                    <img src={category.image} alt="" />
                                                                </div>
                                                                <div className="whates-caption whates-caption2">
                                                                    <h4><Link href="#">{category.title}</Link></h4>
                                                                    <span>{category.author}   -   {category.date}</span>
                                                                    <p>{category.description}</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    ))}
                                                </div>
                                            </div>
                                            {/* card fure */}
                                            <div className="tab-pane fade" id="nav-technology" role="tabpanel" aria-labelledby="nav-technology-tab">
                                                <div className="row">
                                                    {Cdata.map((category) => (
                                                        <div className="col-xl-6 col-lg-6 col-md-6">
                                                            <div className="whats-news-single">
                                                                <div className="whates-img">
                                                                    <img src={category.image} alt=""/>
                                                                </div>
                                                                <div className="whates-caption whates-caption2">
                                                                    <h4><Link href="#">{category.title}</Link></h4>
                                                                    <span>{category.author}   -   {category.date}</span>
                                                                    <p>{category.description}</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    ))}
                                                </div>
                                            </div>
                                            {/* card Five */}
                                            <div className="tab-pane fade" id="nav-nav-Sport" role="tabpanel" aria-labelledby="nav-Sports">
                                                <div className="row">
                                                    {Cdata.map((category) => (
                                                        <div className="col-xl-6 col-lg-6 col-md-6">
                                                            <div className="whats-news-single">
                                                                <div className="whates-img">
                                                                    <img src={category.image} alt=""/>
                                                                </div>
                                                                <div className="whates-caption whates-caption2">
                                                                    <h4><Link href="#">{category.title}</Link></h4>
                                                                    <span>{category.author}   -   {category.date}</span>
                                                                    <p>{category.description}</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    ))}
                                                </div>
                                            </div>
                                        </div>
                                        {/* End Nav Card */}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-4">
                            {/* Flow Socail */}
                            <div className="single-follow mb-45">
                                <div className="single-box">
                                    {Follow.map((follow) => (
                                        <div className="follow-us d-flex align-items-center">
                                            <div className="follow-social">
                                                <Link to={'/'}><img src={follow.image} alt="" /></Link>
                                            </div>
                                            <div className="follow-count">
                                                <span>{follow.follower}</span>
                                                <p>{follow.title}</p>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                            </div>
                            {/* New Poster */}
                            <div className="news-poster d-none d-lg-block pt-4">
                                <img src={require('../../assets/img/news/news_card.jpg')} alt="" />
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </>
    )
}

export default CategoryBanner