import React from 'react';
import { Link } from 'react-router-dom';
import { Fdata } from './Data/FooterData';

function Footer() {
    return (
        <>
            <footer>
                <div className="footer-main footer-bg">
                    <div className="footer-area footer-padding">
                        <div className="container">
                            <div className="row d-flex justify-content-between pt-4">
                                <div className="col-xl-3 col-lg-3 col-md-5 col-sm-8">
                                    <div className="single-footer-caption mb-50">
                                        <div className="single-footer-caption mb-30">
                                            <div className="footer-logo">
                                                <Link href="index.html"><h2>NEWS</h2></Link>
                                            </div>
                                            <div className="footer-tittle">
                                                <div className="footer-pera">
                                                    <p className="info1">Lorem ipsum dolor sit amet, nsectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore.</p>
                                                    <p className="info2"><i class="fa fa-phone pe-1"/>Taukhle, Godawari-3, Lalitpur, Nepal</p>
                                                    <p className="info2"><i class="fa fa-phone pe-1"/> +880166 253 232</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-xl-4 col-lg-4 col-md-5 col-sm-7">
                                    <div className="single-footer-caption mb-50">
                                        <div className="footer-tittle">
                                            <h4>Popular post</h4>
                                        </div>
                                        {Fdata.map((footer)=>(
                                        <div className="whats-right-single mb-2">
                                            <div className="whats-right-img">
                                                <img src={footer.image} alt="" />
                                            </div>
                                            <div className="whats-right-cap">
                                                <h4><Link href="latest_news.html">{footer.title}</Link></h4>
                                                <p>{footer.user}  |  {footer.date}</p>
                                            </div>
                                        </div>
                                        ))}
                                    </div>
                                </div>
                                <div className="col-xl-3 col-lg-3 col-md-5 col-sm-7">
                                    <div className="single-footer-caption mb-4">
                                        <div className="banner">
                                            <img src={require('../assets/img/body_card4.png')} alt="" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="footer-bottom-area footer-bg">
                        <div className="container">
                            <div className="footer-border">
                                <div className="row d-flex align-items-center">
                                    <div className="col-xl-12 ">
                                        <div className="footer-copy-right text-center">
                                            <p>
                                                Copyright © All rights reserved | by <Link to={'/'} target="_blank">Rajesh Tudu</Link>
                                                </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </footer>


        </>
    )
}

export default Footer