import React from 'react'
import { Link } from 'react-router-dom'
import { Post1, Post2, Single } from '../Data/HomeData'
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import { Autoplay, Pagination, Navigation } from "swiper";

function HomeTrending() {
    return (
        <>
            <div className="bg-sky pd-top-80 pd-bottom-50" id="latest">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-3 col-sm-6">
                            <div className="single-post-wrap style-overlay-bg">
                                <div className="thumb">
                                    <img src={require('../../assets/img/post/9.png')} className="img-fluid" alt="img" />
                                </div>
                                <div className="details">
                                    <div className="post-meta-single mb-3">
                                        <ul>
                                            <li><Link className="tag-base tag-blue" to="">fashion</Link></li>
                                            <li><p><i className="fa fa-clock-o" />08.22.2020</p></li>
                                        </ul>
                                    </div>
                                    <h6 className="title"><Link to="#">A Comparison of the Sony FE 85mm f/1.4 GM and Sigma</Link></h6>
                                </div>
                            </div>
                            <div className="banner-one my-5">
                                <img src={require('../../assets/img/gallery/body_card1.png')} alt="" />
                            </div>
                        </div>
                        <div className="col-lg-3 col-sm-6">
                            {Post1.map((post1) => (
                                <div className="single-post-wrap">
                                    <div className="thumb">
                                        <img src={post1.image} alt="img" className='img-fluid' />
                                        <p className="btn-date"><i className="fa fa-clock-o" />08.22.2020</p>
                                    </div>
                                    <div className="details">
                                        <h6 className="title"><Link to="#">{post1.title}</Link></h6>
                                    </div>
                                </div>
                            ))}
                        </div>
                        <div className="col-lg-3 col-sm-6">
                            {Post2.map((post2) => (
                                <div className="single-post-wrap">
                                    <div className="thumb">
                                        <img src={post2.image} alt="img" className='img-fluid' />
                                        <p className="btn-date"><i className="fa fa-clock-o" />{post2.date}</p>
                                    </div>
                                    <div className="details">
                                        <h6 className="title"><Link to="#">{post2.title}</Link></h6>
                                    </div>
                                </div>
                            ))}
                        </div>
                        <div className="col-lg-3 col-sm-6">
                            <div className="trending-post style-box">
                                <div className="section-title">
                                    <h6 className="title">Trending News</h6>
                                </div>
                                <div className="post-slider owl-carousel">
                                    <Swiper
                                        spaceBetween={30}
                                        centeredSlides={true}
                                        autoplay={{
                                            delay: 2500,
                                            disableOnInteraction: false,
                                        }}
                                        pagination={{
                                            clickable: true,
                                        }}
                                        navigation={true}
                                        modules={[Autoplay, Pagination, Navigation]}
                                        className="mySwiper"
                                    >
                                        <SwiperSlide>
                                            <div className="item">
                                                {Single.map((single)=>(
                                                    <div className="single-post-list-wrap">
                                                    <div className="media">
                                                        <div className="media-left">
                                                            <img src={single.image} alt="img" className='img-fluid' />
                                                        </div>
                                                        <div className="media-body">
                                                            <div className="details">
                                                                <div className="post-meta-single">
                                                                    <ul>
                                                                        <li><i className="fa fa-clock-o" />{single.date}</li>
                                                                    </ul>
                                                                </div>
                                                                <h6 className="title"><Link to="#">{single.title}</Link></h6>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                ))}
                                                
                                            </div>
                                        </SwiperSlide>
                                        <SwiperSlide>
                                        <div className="item">
                                                {Single.map((single)=>(
                                                    <div className="single-post-list-wrap">
                                                    <div className="media">
                                                        <div className="media-left">
                                                            <img src={single.image} alt="img" className='img-fluid' />
                                                        </div>
                                                        <div className="media-body">
                                                            <div className="details">
                                                                <div className="post-meta-single">
                                                                    <ul>
                                                                        <li><i className="fa fa-clock-o" />{single.date}</li>
                                                                    </ul>
                                                                </div>
                                                                <h6 className="title"><Link to="#">{single.title}</Link></h6>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                ))}
                                                
                                            </div>
                                        </SwiperSlide>
                                    </Swiper>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </>
    )
}

export default HomeTrending