import React from 'react';
import { Link } from 'react-router-dom';
import { HBdata } from '../Data/HomeData';

function HomeBanner() {
    return (
        <>
        <div className="banner-area banner-inner-1 bg-black" id="banner">
            <div className="banner-inner pt-5">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-6">
                            <div className="thumb after-left-top">
                                <img src={require('../../assets/img/banner/1.png')} className="img-fluid" alt="" />
                            </div>
                        </div>
                        <div className="col-lg-6 align-self-center">
                            <div className="banner-details mt-4 mt-lg-0">
                                <div className="post-meta-single">
                                    <ul>
                                        <li><Link className="tag-base tag-orange" to={'/'}>Tech</Link></li>
                                        <li className="date"><i className="fa fa-clock-o" />08.22.2020</li>
                                    </ul>
                                </div>
                                <h2>ReZoom outage left some people locked out.</h2>
                                <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. </p>
                                <Link className="btn btn-blue" to={'/'}>Read More</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="container">
                <div className="row">
                    {HBdata.map((banner)=>(
                    <div className="col-lg-3 col-sm-6">
                        <div className="single-post-wrap style-white">
                            <div className="thumb">
                                <img src={banner.image} className='img-fluid' alt="" />
                                <Link className="tag-base tag-orange text-capitalize" to={'/'}>{banner.tag}</Link>
                            </div>
                            <div className="details">
                            <Link to={'/'}><h6 className="title">{banner.title}</h6></Link>
                                <div className="post-meta-single mt-3">
                                    <ul>
                                        <li><i className="fa fa-clock-o" />{banner.date}</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                    ))}
                </div>
            </div>
        </div>

        </>
    )
}

export default HomeBanner